@import "../../../scss/settings/colors";
@import "../../../scss/settings/mixins";

.login__content-form {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  .login__form-inputs {
    padding: 2rem 0 1rem 0;
    background-color: $colorFormBackground;
    width: 100%;
    max-width: 385px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

.login__form-input {
  min-width: 250px;
  .login__input-label {
    display: block;
    font-size: 1rem;
    font-weight: 600;
    margin-bottom: 2rem;
  }
  .login__input-input {
    font-size: 1rem;
    padding: 0;
    display: block;
    width: 100%;
    border: none;
    background-color: $colorFormBackground;
    border-bottom: 1px solid $colorPagination;
    outline: none;
    margin-bottom: 2rem;
    &:focus {
      border-bottom: 1px solid $colorAccent;
    }
  }
}

.login__form-input {
  .login__input-input.error {
    border-bottom: 1px solid $colorError;
    margin-bottom: 0.4rem;
    &:focus {
      border-bottom: 1px solid $colorAccent;
    }
  }
}

.login__error-email,
.login__error-password,
.login__error-form {
  text-align: center;
  display: block;
  color: $colorError;
  font-size: 0.9rem;
  margin-bottom: 0.7rem;
}

.login__error-form {
  height: 1rem;
}

// Buttons
.login__form-buttons {
  margin-top: 1rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  @include mediumScreen {
    margin-top: 3rem;
  }
  .login__button-register,
  .login__button-login {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 117px;
    height: 50px;
    border-radius: 2px;
    border: 1px solid $colorFontPrimary;
    font-size: 1.1rem;
    color: $colorFontSecondary;
  }
  .login__button-login {
    cursor: pointer;
    background-color: $colorAccent;
    &:disabled {
      border: 1px solid $colorPaginationHover;
      color: $colorPaginationHover;
    }
    @include mediumScreen {
      background-color: transparent;
      &:hover {
        background-color: $colorFormBackground;
      }
      &:disabled {
        cursor: not-allowed;
        box-shadow: none;
      }
    }
  }
  .login__button-register {
    @include mediumScreen {
      border: 1px solid $colorBackgroundPrimary;
      &:hover {
        border: 1px solid $colorPaginationHover;
      }
    }
  }
}
